import React, { useState } from "react";
import {
  Button,
  Table,
  TableCell,
  TableBody,
  TableRow,
  TableHead,
  Divider,
  makeStyles,
  IconButton,
  Typography,
  Card,
} from "@material-ui/core";
import AddBoxIcon from "@material-ui/icons/AddBox";
import { MNG_DELETE_WORK_URL, U_EDIT_ALLOCATION } from "src/settings";
import { deleteFetch, postFetch } from "src/base";
import corfirmModal from "src/components/ConfirmModal";
import alertBox from "src/components/AlertBox";
import EditWork from "./EditWork";
const useStyles = makeStyles((theme) => ({
  root: {
    // flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(2),
  },
  header: {
    display: "flex",
    alignItems: "center",
    paddingBottom: theme.spacing(2),
    "& .MuiTextField-root": {
      width: "150px",
    },
    "& .MuiButton-root": {
      width: "150px",
      height: "40px",
    },
  },
  empty: {
    paddingTop: theme.spacing(1),
    textAlign: 'center'
  },
}));
const WORK_TYPE = {
  100: "",
  200: "",
  310: "",
  320: "",
  330: "",
};
export default function WorkTable(props) {
  const { works, refresh, workType, planStageId, hasPermission } = props;
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [workDetail, setWorkDetail] = useState({});
  const deleteWork = (id, name) => {
    const cor = corfirmModal({
      title: `[${name}]`,
      handleCorfirm: () => {
        cor.close();
        deleteFetch({
          url: `${MNG_DELETE_WORK_URL}?id=${id}`,
          successCallback: refresh,
        });
      },
    });
  };
  const handleUpdate = (id, finished) => {
    postFetch({
      url: U_EDIT_ALLOCATION,
      values: {
        id,
        finished
      },
      successCallback: () => {
        alertBox({ text: "", severity: "success" });
        refresh();
      }
    });
  }
  return (
    <Card className={classes.root}>
      <div className={classes.header}>
        <Typography color="textPrimary" size="small" component="h2">
          {`${WORK_TYPE[workType]}`}
        </Typography>
        {hasPermission && (
          <IconButton color="inherit" onClick={() => setOpen(true)}>
            <AddBoxIcon />
          </IconButton>
        )}
      </div>
      <Divider />
      {works?.length > 0 ? (
        <Table>
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell></TableCell>
              {!hasPermission && (
                <>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                </>
              )}
              <TableCell align="center"></TableCell>
            </TableRow>
          </TableHead>
          {/* type; model; name; principal;
          inventory; inventoryUnit; */}
          <TableBody>
            {works?.map((work) => (
              <TableRow hover key={work.id}>
                <TableCell>{work.id}</TableCell>
                <TableCell>{work.name}</TableCell>
                {!hasPermission && (
                  <>
                    <TableCell>
                      {`${work?.allocation?.planWorkStartDate || "--"} ~ ${
                        work?.allocation?.planWorkEndDate || "--"
                      }`}
                    </TableCell>
                    <TableCell>
                      {work?.allocation?.finished === 0 ? "" : ""}
                    </TableCell>
                  </>
                )}
                <TableCell align="center">
                  {hasPermission ? (
                    <>
                      <Button
                        color="primary"
                        size="small"
                        variant="text"
                        onClick={() => {
                          setOpen(true);
                          setWorkDetail(work);
                        }}
                      >
                        
                      </Button>
                      <Button
                        color="primary"
                        size="small"
                        variant="text"
                        onClick={() => deleteWork(work.id, work.name)}
                      >
                        
                      </Button>
                    </>
                  ) : (
                    <Button
                      color="primary"
                      size="small"
                      variant="text"
                      onClick={() =>
                        handleUpdate(
                          work?.allocation?.id,
                          work?.allocation?.finished === 0 ? 1 : 0
                        )
                      }
                      hasPermission={hasPermission}
                    >
                      {work?.allocation?.finished === 0
                        ? ""
                        : ""}
                    </Button>
                  )}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      ) : (
        <p className={classes.empty}></p>
      )}
      <EditWork
        open={open}
        onClose={() => {
          setOpen(false);
          setWorkDetail({});
          refresh();
        }}
        workDetail={workDetail}
        planStageId={planStageId}
        workType={workType}
      />
    </Card>
  );
}
