import {Redirect} from 'react-router-dom';
import MainLayout from './layouts/MainLayout';
import DashboardLayout from './layouts/DashoardLayout'
import LoginView from './views/LoginView';
import ResetPasswordView from "./views/ResetPasswordView";
import UpdateUserInfoView from "./views/updateUserInfo/UpdateUserInfoView";
import NotFoundView from './views/NotFoundView';
import SeminarView from './views/seminar/SeminarView';
import RecorderView from './views/recorder/RecorderView';
import UserManagementView from './views/userManagemet/UserManagementView';
import EssayRecommendationView from "./views/essayRecommendation/EssayRecommendationView";
import EssaysView from "./views/essayRecommendation/essays";
import ClassesView from "./views/essayRecommendation/classes";
import ArticleView from './views/ArticleView';
import ArticleEditView from './views/ArticleEditView'
import TrainingSchemeView from './views/trainingScheme/TrainingSchemeView';
import StudyPlanView from "./views/studyPlan";
import PlanAllocationView from "./views/studyPlan/planAllocation";
// import StudyPlanEditView from "./views/studyPlan/edit";
import StudyPlanDetailView from "./views/studyPlan/detail";
import BulletinManageView from "./views/bulletin/BulletinManageView";
import BulletinManagementView from "./views/bulletin/BulletinManagementView";
import BulletinListView from './views/bulletin/BulletinListView';
import DeviceView from './views/device/DeviceView';
       
const routes = [
  {
    path: "/main",
    component: MainLayout,
    routes: [
      { path: "/main/login", component: LoginView },
      { path: "/main/reset", component: ResetPasswordView },
    ], 
  },
  {
    path: "/app",
    component: DashboardLayout,
    auth: true,
    routes: [
      { path: "/app/updateUserInfo/:id", component: UpdateUserInfoView }, //
      { path: "/app/article/:id", component: ArticleView }, //
      { path: "/app/articleEdit/:id", component: ArticleEditView }, //
      { path: "/app/seminar", component: SeminarView }, //
      { path: "/app/recorder", component: RecorderView }, //
      { path: "/app/userManagement", component: UserManagementView }, //
      { path: "/app/essayRecommendation/essays", component: EssaysView }, //
      {
        path: "/app/essayRecommendation/classes",
        component: ClassesView,
      }, //
      { path: "/app/essayRecommendation", component: EssayRecommendationView }, //
      {
        path: "/app/studyPlan/planAllocation/:planId",
        component: PlanAllocationView,
      }, //
      { path: "/app/studyPlan/detail/:id", component: StudyPlanDetailView }, //
      { path: "/app/studyPlan", component: StudyPlanView }, //
      { path: "/app/trainingScheme", component: TrainingSchemeView }, //
      { path: "/app/bulletinManage", component: BulletinManageView }, // 
      { path: "/app/bulletinManagement", component: BulletinManagementView }, // 
      { path: "/app/bulletinList", component: BulletinListView }, //
      { path: "/app/device", component: DeviceView }, //IT
      { path: "*", component: NotFoundView },
    ],
  },
  {
    path: "/",
    component: MainLayout,
    routes: [
      { path: "/404", component: NotFoundView },
      { path: "/*", component: LoginView },
    ],
  },
];

export default routes;