import React from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import { postFetch } from "src/base";
import { Box, Button, TextField, Typography, Dialog } from "@material-ui/core";
import { ADD_USER_URL } from "src/settings";
const userRoles = [
  {
    value: 10,
    label: "",
  },
  {
    value: 20,
    label: "",
  },
  {
    value: 30,
    label: "Lab",
  },
  {
    value: 40,
    label: "",
  },
  {
    value: 50,
    label: "",
  },
  {
    value: 60,
    label: "",
  },
  {
    value: 0,
    label: "",
  },
];
function UserManageForm(props) {
  const { onClose, open } = props;

  const handleClose = () => {
    onClose();
  };

  return (
    <Dialog onClose={handleClose} open={open}>
      <Formik
        initialValues={{
          name: "",
          stuId: "",
          roleId: 0,
        }}
        validationSchema={Yup.object().shape({
          name: Yup.string().max(255).required(),
          stuId: Yup.string().max(255).required(),
          roleId: Yup.number().required(),
        })}
        onSubmit={(values) => {
          postFetch({
            url: ADD_USER_URL,
            values,
            successCallback: () => {
              console.log("");
              handleClose();
            },
          });
        }}
      >
        {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          isSubmitting,
          touched,
          values,
        }) => (
          <form onSubmit={handleSubmit} className="dialogForm">
            <Box mb={1}>
              <Typography color="textPrimary" variant="h4">
                
              </Typography>
            </Box>
            <TextField
              error={Boolean(touched.name && errors.name)}
              fullWidth
              label=""
              margin="normal"
              name="name"
              onBlur={handleBlur}
              onChange={handleChange}
              variant="outlined"
            />
            <TextField
              error={Boolean(touched.stuId && errors.stuId)}
              fullWidth
              label="/"
              margin="normal"
              name="stuId"
              onBlur={handleBlur}
              onChange={handleChange}
              variant="outlined"
            />
            <TextField
              select
              fullWidth
              label=""
              name="roleId"
              margin="normal"
              onChange={handleChange}
              variant="outlined"
              SelectProps={{
                native: true,
              }}
              value={values.roleId}
            >
              {userRoles.map((option) => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </TextField>
            <Box my={2}>
              <Button
                color="primary"
                fullWidth
                size="large"
                type="submit"
                variant="contained"
              >
                
              </Button>
            </Box>
          </form>
        )}
      </Formik>
    </Dialog>
  );
}
export default UserManageForm;
