import React from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import {
  Box,
  Button,
  TextField,
  Dialog,
  makeStyles,
  DialogTitle,
} from "@material-ui/core";
import alertBox from "src/components/AlertBox";
import { MNG_ADD_DEVICE_URL, MNG_UPDATE_DEVICE_URL } from "src/settings";
import { postFetch } from "src/base";
const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(3)
  },
}));

export default function EditDevice(props) {
  const { onClose, open, deviceDetail } = props;
  const classes = useStyles();

  const handleClose = () => {
    onClose();
  };
  return (
    <Dialog onClose={handleClose} open={open} className={classes.root}>
      <DialogTitle onClose={handleClose}>
        {deviceDetail?.id ? "" : ""}
      </DialogTitle>
      <Formik
        initialValues={deviceDetail}
        // validationSchema={Yup.object().shape({
        //   type: Yup.number().required(""),
        // })}
        onSubmit={(values) => {
          postFetch({
            url:
              deviceDetail?.id > 0 ? MNG_UPDATE_DEVICE_URL : MNG_ADD_DEVICE_URL,
            values: {
              ...values,
              type: values?.type || 0
            },
            successCallback: () => {
              alertBox({ text: "", severity: "success" });
              handleClose();
            },
          });
        }}
      >
        {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          isSubmitting,
          touched,
          values,
        }) => (
          <form onSubmit={handleSubmit} className="dialogForm">
            <TextField
              select
              fullWidth
              label=""
              name="type"
              margin="normal"
              onChange={handleChange}
              variant="outlined"
              SelectProps={{
                native: true,
              }}
            >
              <option value={0}> </option>
              <option value={1}> </option>
              <option value={2}> </option>
              <option value={3}> </option>
            </TextField>
            <TextField
              label=""
              fullWidth
              margin="normal"
              name="model"
              value={values.model}
              variant="outlined"
              onBlur={handleBlur}
              onChange={handleChange}
            />
            <TextField
              label=""
              fullWidth
              margin="normal"
              name="name"
              value={values.name}
              variant="outlined"
              onBlur={handleBlur}
              onChange={handleChange}
            />
            <TextField
              label=""
              margin="normal"
              fullWidth
              name="principal"
              value={values.principal}
              variant="outlined"
              onBlur={handleBlur}
              onChange={handleChange}
            />
            <TextField
              label=""
              margin="normal"
              fullWidth
              name="inventory"
              value={values.inventory}
              variant="outlined"
              onBlur={handleBlur}
              onChange={handleChange}
            />
            <TextField
              label=""
              margin="normal"
              fullWidth
              name="inventoryUnit"
              value={values.inventoryUnit}
              variant="outlined"
              onBlur={handleBlur}
              onChange={handleChange}
            />

            <Box my={2}>
              <Button
                color="primary"
                // disabled={isSubmitting}
                size="large"
                type="submit"
                variant="contained"
              >
                
              </Button>
            </Box>
          </form>
        )}
      </Formik>
    </Dialog>
  );
}
